import { useState } from "react";
import { Button } from "react-bootstrap";

import style from "./../FilterMain.module.scss";

import Filter from "./../../UI/Filter/Filter";
import FilterYears from "./../../UI/Filter/FilterYears";

import countries from "./../../data/countries";
import year from "./../../data/year";
import raiting from "./../../data/raiting";
import geners from "./../../data/genres";

const FilterMain = (props) => {
  const {
    search,
    setSearch,
    inputValueCountry,
    setInputValueCountry,
    inputValueGenre,
    setInputValueGenre,
    firstRaiting,
    setFirstRaiting,
    lastRaiting,
    setLastRaiting,
    firstYear,
    setFirstYear,
    lastYear,
    setLastYear,
  } = props;

  return (
    <div className={style.Filter}>
      <h1>Фильтр</h1>
      <Filter
        data={countries}
        nameProps="country"
        nameFilt="страна"
        inputValue={inputValueCountry}
        setInputValue={setInputValueCountry}
      />
      <Filter
        data={geners}
        nameProps="genre"
        nameFilt="жанр"
        inputValue={inputValueGenre}
        setInputValue={setInputValueGenre}
      />
      <FilterYears
        name="Рейтинг"
        arrayInfo={[...raiting]}
        mirrorIndex={false}
        first={[...firstRaiting]}
        setFirst={setFirstRaiting}
        last={lastRaiting}
        setLast={setLastRaiting}
      />
      <FilterYears
        name="Год"
        arrayInfo={[...year]}
        mirrorIndex={true}
        first={[...firstYear]}
        setFirst={setFirstYear}
        last={lastYear}
        setLast={setLastYear}
      />
      <Button onClick={() => setSearch(true)}>Найти</Button>
    </div>
  );
};

export default FilterMain;
